.aside{position:fixed;top:100px;left:25px;width:120px;height:40rem;padding:2rem 0;opacity:1;transition:opacity 0.2s 0.5s ease}.aside--hidden{opacity:0;transition:opacity 0.2s ease}.aside__bar{width:2px;height:100%;background:#e9e9e9;opacity:0.3;position:absolute;top:10px;left:17px}.aside__list{height:100%;display:flex;flex-direction:column;justify-content:space-between}.aside__section{display:flex;justify-content:space-between;padding-left:30px;position:relative}.aside__section-index{position:absolute;left:30px;opacity:0.5;transition:transform .5s ease}.aside__section-name{opacity:0;text-transform:uppercase;letter-spacing:0.12em;transition:transform .5s .2s ease, opacity .5s .2s ease;transform:translateX(30px)}.aside__section--active .aside__section-index{opacity:1;transform:translateX(-40px)}.aside__section--active .aside__section-name{opacity:1;transform:translateX(0)}@media (max-width: 900px){.aside{display:none}}

.content{position:relative;transition:transform 0.3s 0.1s ease, opacity 0.3s 0.1s ease;transform-origin:left}.content.moved{transition:transform 0.3s 0.1s ease, opacity 0.3s 0.1s ease;transform:translateX(-50%);opacity:0.5}@media (max-width: 450px){.content.moved{transform:translateX(-90%)}}

.loading{height:100vh;width:100%;position:absolute;top:0;left:0;display:flex;flex-direction:column;align-items:center;justify-content:center}.loading__spinner{width:10em;height:10em;border:1em solid transparent;border-color:#e9e9e9;border-top-color:#a34bed;border-radius:50%;-webkit-animation:spin 1s linear infinite;animation:spin 1s linear infinite;margin-top:4rem}.loading__title{text-transform:uppercase;letter-spacing:0.12em;margin-top:4rem;font-size:4rem;font-weight:bold}@-webkit-keyframes spin{100%{transform:rotate(360deg)}}@keyframes spin{100%{transform:rotate(360deg)}}

.nav{display:flex;justify-content:space-between;align-items:center;height:8rem;background:#0e0e0e;padding:2rem 5rem;position:fixed;top:0;left:0;z-index:100;width:100%;box-shadow:0 0 1px #e9e9e9}.nav__brand{display:flex;align-items:center}.nav__logo{height:3rem}.nav__logo-text{display:inline-block;text-transform:uppercase;font-weight:bold;margin-left:2rem;letter-spacing:0.05em}.nav__burger{width:40px;height:30px;display:flex;flex-direction:column;align-items:flex-end;justify-content:space-between;background:transparent;padding:4px;border:none;cursor:pointer;position:relative}.nav__burger-part{width:100%;height:2px;background:#e9e9e9;transition:opacity 0.2s 0.2s ease, transform 0.2s 0.2s ease}.nav__burger-part:nth-child(2){transition:opacity 0.2s 0.2s ease, transform 0.2s 0.2s ease;width:80%}.nav__burger--open .nav__burger-part:first-child{transform:translateY(10px) rotate(45deg)}.nav__burger--open .nav__burger-part:nth-child(2){opacity:0;transition:opacity 0.2s ease, transform 0.2s ease;transform:translateX(500%)}.nav__burger--open .nav__burger-part:last-child{transform:translateY(-10px) rotate(-45deg)}.nav__list{font-size:1.6rem;position:absolute;padding:20px 50px;right:0;top:100%;max-width:100%;list-style:none;opacity:0;transition:opacity 0.3s ease-in-out, transform 0.2s 0.1s ease-in-out;transform:scaleY(0);transform-origin:top}.nav__list--active{opacity:1;transition:opacity 0.3s 0.2s ease-in-out, transform 0.3s 0.1s ease-in-out;transform:scaleY(1)}.nav__list-item{font-size:3em;letter-spacing:0.12em;cursor:pointer;display:block;padding:0.5em;position:relative;background:transparent;color:#e9e9e9;border:none}.nav__list-item::after{content:'';position:absolute;top:0;left:0;width:2em;height:100%;z-index:-1;background:#FF5F51;transition:transform 0.1s linear;transform:scaleX(0);transform-origin:left;opacity:0.9}.nav__list-item:hover::after{transform:scaleX(1)}.nav__list-item--active{color:#FF5F51}.nav__list-item--active::after{background:#e9e9e9}@media (max-width: 450px){.nav{padding:1rem}}

.section{display:flex;flex-direction:column;align-items:center;padding:6rem 0 2rem;width:60%;margin:0 auto}.section__title{letter-spacing:0.1em;margin-bottom:8rem;font-size:2.5em;position:relative}.section__title::after{content:'';position:absolute;top:0;left:0;width:50px;height:100%;background:#FF5F51;z-index:-1;transform:rotate(-10deg) translateX(-25px)}

